.FEEDBACK-DIV {
  display: flex;
  justify-content: center;
}

.feedback-main-parent-div {
  display: grid;
  justify-content: center;
  grid-template-columns: 1fr 2fr 1fr;
  width: 850px;
}

.signature-div {
  display: grid;
  gap: 20px;
}

.signature-parent-div {
  display: grid;
  gap: 80px;
}

.signature-line-div {
  display: grid;
  border-bottom: 1px solid black;
  padding-bottom: 40px;
  font-weight: 700;
}

.lecturer-comments-div {
  font-weight: 700;
}