.details-main-div {
  display: grid;
  justify-content: center;
  padding-bottom: 80px;
  border-bottom: 1px solid black;
}

.daily_lesson_plan_p {
  font-size: 20px;
  font-weight: 700;
}

.DETAILS-DIV {
  display: grid;
  border: 2px solid black;
