.HOME-DIV {
  display: grid;
  gap: 80px;
  padding-top: 50px;
}

.home_activateAskSave-div {
  position: fixed;
  display: grid;
  grid-template-columns: 1fr;
  justify-content: space-evenly;
  align-items: center;
  top: 40%;
  left: 40%;
  transform : translate(60% -60%);
  gap: 20px;
  border: 2px solid black;
  background-color: burlywood;
  z-index: 1;
  width: 300px;
  height: 100px;
}

.home_activateAskSaveHeading-div {
  display: flex;
  justify-content: center;
  font-weight: 700;
  background: darkgray;
  height: 92%;
  align-items: center;
  border-bottom: 2px solid black;
}

.home_activateAskSaveChild-div {
  display: grid;
  grid-template-columns: 1fr 1fr;
}

.home_createNewCancelChanges-button {
  border: 1px solid black;
  border-radius: 20px;
  padding: 8px 10px;
  cursor: pointer;
  max-height: 50px;
  width: 100px;
}

.home_createNewCancelChanges-button:hover {
  background-color: grey;
}