.STAGE-DIV {
  display: grid;
  justify-content: center;
  border-bottom: 1px solid black;
  padding-bottom: 80px;
}

.stage-main-parent-div {
  display: grid;
  width: 850px;
}

.stage-div {
  display: grid;
  grid-template-columns: 0.6fr 1fr 1fr 1fr;
  border-left: 2px solid black;
}

.stage-div:nth-child(1) {
  border-top: 2px solid black;
}

.stage-headings {
  font-weight: 700;
  display: grid;
  border-bottom: 2px solid black;
  border-right: 2px solid black;
  justify-content: center;
  align-items: start;
  padding: 10px 20px;
  background-color: lightgreen;
}

.stage-headings-first {
  font-weight: 700;
  display: grid;
  border-bottom: 2px solid black;
  border-right: 2px solid black;
  justify-content: center;
  align-items: center;
  padding: 10px 20px;
  background-color: greenyellow;
}

.stage-contents {
  display: grid;
  border-bottom: 2px solid black;
  border-right: 2px solid black;
  justify-content: center;
  align-items: start;
  gap: 20px;
  padding: 20px 0px;
}

.stage-contents-input {
  display: grid;
  border-bottom: 2px solid black;
  border-right: 2px solid black;
  justify-content: center;
  align-items: start;
  gap: 20px;
  padding: 20px 0px;
}

.stage-buttons-div {
  display: flex;
  position: relative;
  justify-content: center;
  align-items: center;
  gap: 20px;
}

.stage-button {
  cursor: pointer;
}

.stage-unstatic-div {
  display: flex;
  gap: 10px;
}

.stage-inputs {
  max-height: 15px;
}