.NAVBAR-DIV {
  position: fixed;
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  top: 0px;
  height: 40px;
  background-color: darkcyan;
  border-top: 2px solid black;
  border-bottom: 2px solid black;
  width: 100%;
  z-index: 3;
}

.parent_navbar_buttons-div {
  display: flex;
  justify-content: end;
}

.parent_navbar_buttons-div:nth-child(3) {
  display: flex;
  justify-content: end;
}

.navbar_title-div {
  width: 202px;
  display: flex;
  border-right: 2px solid black;
  justify-content: center;
  align-items: center;
  font-weight: 700;
}

.navbar_title-button {
  cursor: pointer;
  border: none;
  background-color: transparent;
  font-weight: 700;
  color: darkred;
  font-size: 15px;
}

.navbar_title-button:hover {
  color: brown;
}

.navbar-button {
  border: none;
  background-color: transparent;
  height: 100%;
  width: 100%;
  font-weight: 700;
  cursor: pointer;
}

.navbar-button:hover {
  background-color: lightgoldenrodyellow;
}

.parent_navbar_searchbar-div {
  display: grid;
  place-items: center;
  width: 250px;
}

.navbar_search_bar-button {
  cursor: pointer;
  border-radius: 5px;
  border: 1px solid black;
  font-weight: 500;
}

.navbar_search_bar-button:hover {
  color: darkgrey;
}

.navbar_searchbar_input {
  display: flex;
  width: 250px;
}

.navbar_profile_buttons-div {
  display: flex;
  background-color: lightblue;
  border: 1px solid black;
  border-radius: 50%;
  position: absolute;
}

.navbar_profile_buttons-div:hover {
  background-color: lightyellow;
}

.navbar_profile-button {
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
  height: 100%;
  background-color: transparent;
  border: none;
}

.navbar_parent_profile_buttons-div {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 40px;
}

.username-button {
  cursor: pointer;
  height: 100%;
  width: 100%;
  background-color: transparent;
  border: none;
  font-weight: 700;
  font-size: 15px;
  color: darkred;
}

.username-button:hover {
  color: blanchedalmond;
}

.navbar_username-div {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100px;
}

.navbar_logout_buttons-div {
  border-right: 2px solid black;
  border-left: 2px solid black;
  width: 100px;
  background-color: cyan;
}

.navbar_buttons-div {
  border-right: 2px solid black;
  width: 100px;
  display: grid;
  background-color: cyan;
}

.navbar_yearButtonDskp-div {
  position: absolute;
  display: grid;
  border-bottom: none;
  top: 42px;
  left: 202px;
  width: 104px;
  background-color: aquamarine;
  height: 200px;
}

.navbar_yearButtonSow-div {
  position: absolute;
  display: grid;
  border-bottom: none;
  top: 42px;
  left: 304px;
  width: 104px;
  background-color: aquamarine;
  height: 200px;
}

.navbar_yearButtonTextbook-div {
  position: absolute;
  display: grid;
  border-bottom: none;
  top: 42px;
  left: 406px;
  width: 104px;
  background-color: aquamarine;
  height: 200px;
}

.navbar_year-button {
  background-color: transparent;
  cursor: pointer;
  border: 2px solid black;
  border-top: none;
  font-weight: 700;
  color: orangered;
  font-size: 16px;
}

.navbar_year-button:hover {
  background-color: beige;
}

.navbar_displayUser-div {
  position: absolute;
  display: grid;
  top: 30px;
  left: 537px;
  border: 1px solid black;
  background-color: lightgrey;
  width: 256px;
  max-height: 200px;
  overflow-y: auto;
}

.navbar_displayUser-div::-webkit-scrollbar {
  display: none; 
}

.navbar_user-button {
  background-color: transparent;
  border: none;
  cursor: pointer;
  height: 40px;
}

.navbar_user-button:hover {
  background-color: grey;
}

.navbar_searchbarAndButton-div {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 20px;
}

.navbar_showMore-button {
  display: flex;
  justify-content: center;
  cursor: pointer;
  border: none;
  border-top: 1px solid black;
}

.navbar_showMore-button:hover {
  background-color: grey;
}