.SIDEBAR-DIV {
  position: fixed;
  display: flex;
  left: 0px;
  top: 44px;
  width: 204px;
  border: none;
  border-bottom-right-radius: 20px;
  height: 300px;
  z-index: 2;
}

.sidebar_main_buttons-div {
  width: 90%;
  height: 99.2%;
  background-color: darkturquoise;
  border-bottom: 2px solid black;
}

.close_open_button-div {
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}

.close_open-button {
  height: 100%;
  cursor: pointer;
  background-color: transparent;
  border: none;
  border-left: 2px solid black;
  border-right: 2px solid black;
  border-bottom: 2px solid black;
  border-bottom-right-radius: 20px;
  background-color: darkturquoise;
  font-weight: 700;
}

.close_open-button:hover {
  background-color: beige;
}

.save-button {
  width: 100%;
  height: 100%;
  cursor: pointer;
  border: none;
  background-color: transparent;
  font-weight: 700;
}

.save-button:hover {
  background-color: darkkhaki;
}

.download-pdf-button {
  width: 100%;
  height: 100%;
  cursor: pointer;
  border: none;
  background-color: transparent;
  font-weight: 700;
}

.download-pdf-button:hover {
  background-color: darkkhaki;
}

.sidebar-button-div {
  display: flex;
  background-color: cadetblue;
  border-bottom: 1px solid black;
  height: 60px;
}

.sidebar_downloadPdfNaming-div {
  border: 2px solid black;
  height: 288px;
  padding: 5px 8px;
  position: fixed;
  top: 42px;
  left: 180px;
  display: grid;
  grid-template-rows: 1fr 6fr;
  background-color: cadetblue;
}

.sidebar_downloadPdfNamingCreatePdfCancelButton-div {
  display: flex;
  justify-content: space-evenly;
  gap: 10px;
}

.sidebar_downloadPdfNaming-input {
  max-height: 20px;
}

.sidebar_downloadPdfNamingCreatePdfCancel-button {
  cursor: pointer;
  border: 1px solid black;
  padding: 3px 5px;
  border-radius: 20px;
}

.sidebar_downloadPdfNamingCreatePdfCancel-button:hover {
  background-color: darkkhaki;
}
